$font-path:	'../../fonts';

// Cera Font Family
//
//    Black     900     italic
//    Bold      700     italic
//    Medium    500     italic
//    Regular   400     italic
//    Light     200     italic
//    Thin      100     italic

@font-face {
    font-family: 'Cera';
    src: url($font-path+'/cera/cera-black-webfont.eot');
    src: url($font-path+'/cera/cera-black-webfont.eot?#iefix') format('embedded-opentype'),
         url($font-path+'/cera/cera-black-webfont.woff2') format('woff2'),
         url($font-path+'/cera/cera-black-webfont.woff') format('woff'),
         url($font-path+'/cera/cera-black-webfont.ttf') format('truetype'),
         url($font-path+'/cera/cera-black-webfont.svg#cerablack') format('svg');
    font-weight: 900;
    font-style: normal;
}

@font-face {
    font-family: 'Cera';
    src: url($font-path+'/cera/cera-blackitalic-webfont.eot');
    src: url($font-path+'/cera/cera-blackitalic-webfont.eot?#iefix') format('embedded-opentype'),
         url($font-path+'/cera/cera-blackitalic-webfont.woff2') format('woff2'),
         url($font-path+'/cera/cera-blackitalic-webfont.woff') format('woff'),
         url($font-path+'/cera/cera-blackitalic-webfont.ttf') format('truetype'),
         url($font-path+'/cera/cera-blackitalic-webfont.svg#cerablack_italic') format('svg');
    font-weight: 900;
    font-style: italic;
}

